@import "../../../assets/style/variable";
@import "../../../assets/style/mixins";


.buttons {
  display: flex;
  flex-direction: column;
  text-align: center;


  @include onTablet {
    flex-direction: row;
    justify-content: space-around;
  }

  @include onNotebook {
    flex-direction: row;
    justify-content: space-around;
  }
}

.button {
  width: 290px;
  height: 47px;
  font-size: 14px;
  text-align: center;
  margin: 0 auto 10px;

  @include onTablet {
    width: 100%;
    margin: 0 20px 10px 20px;
    height: 47px;
  }

  @include onNotebook {
    margin: 0 20px 10px 20px;
    width: 100%;
    height: 47px;
  }
}