@import '../../../../assets/style/variable';
@import '../../../../assets/style/mixins.scss';

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.image {
  flex: 0 0 178px;
  height: 178px;
  width: 100%;
  border-radius: 15px;
  margin-bottom: 20px;
}

.image img {
  object-fit: cover;
  border-radius: 15px;
  width: 100%;
  height: 100%;
}

.text {
  flex: 1 1 1fr;
  padding: 0 20px;
  font-size: 14px;
}

.title {
  font-size: 18px;
  color: var(--white);
}

.text h2 {
  margin: 0 auto;
  padding: 0;
  margin-bottom: 10px;
}

.info {
  flex: 0 0 1fr;
  display: flex;
  margin-left: 10px;
  margin-top: auto;
  padding: 10px;
}

.info span {
  margin-right: 20px;
}

.eyeIcon {
  width: 19px;
  height: 14px;
  margin-right: 8px;
}

.calendarIcon {
  width: 16px;
  height: 15px;
  margin-right: 8px;
}