@import '../../../../assets/style/variable';
@import '../../../../assets/style/mixins.scss';

.smartBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #213054;
  color: var(--white);
  border-radius: 15px;
  padding: 25px 15px 15px;
  margin-bottom: 20px;
  height: 100%;

  .name {
    width: 100%;
    border-bottom: 1px solid var(--blue);
    p {
      font-size: 12px;
      text-align: center;
      margin: 0;
      padding: 0;
    }

    .contractName {
      justify-content: center;
      align-items: center;
      display: flex;
      margin-bottom: 10px;

      h2 {
        text-align: center;
        margin: 0;
        padding: 0;
        font-size: 24px;
        margin-left: 50px;
        margin-right: 50px;
      }

      .infoIcon {

        div svg {
          width: 17px;
          height: 17px;
    
          path {
            fill: #23BCFA;
          }
    
          &:hover {
            path {
              fill: #23BCFA
            }
          }
        }
      }
    }
  }

  .description {
    width: 100%;
    padding: 20px 0;
    border-bottom: 1px solid rgb(14%, 74%, 98%, 0.2);

    .descriptionItem {
      display: flex;
      align-items: center;

      .iconBlock {
        flex: 0 0 36px;
        display: flex;
        width: 36px;
        height: 36px;
        border-radius: 15%;
        background: rgb(34%, 71%, 71%, 0.1);
        justify-content: center;
        align-items: center;
        margin-right: 10px;
        margin-bottom: 10px;
      }

      .infoIcon {
        margin-left: 10px;

        div svg {
          width: 17px;
          height: 17px;

          path {
            fill: #23BCFA;
          }

          &:hover {
            path {
              fill: #23BCFA
            }
          }
        }
      }

      .descriptionIcon {
        div svg {
          width: 17px;
          height: 17px;

          path {
            fill: #39D2D1;
          }

          &:hover {
            path {
              fill: #23BCFA
            }
          }
        }
      }
    }
  }

  .extraInfo {
    width: 100%;
    margin-bottom: 10px;

    .firstItem {
      text-align: center;
      padding-bottom: 10px;
      border-bottom: 1px solid rgb(14%, 74%, 98%, 0.2);

      p {
        font-size: 12px;
      }
      span {
        font-weight: bold;
        font-size: 14px;
      };
    }

    .secondItem {
      text-align: center;
      padding-bottom: 10px;

      p {
        font-size: 12px;
      }

      div {
        display: flex;
        justify-content: center;

        span {
          font-weight: bold;
          font-size: 14px;
        };

        .infoIcon {
          margin-left: 10px;
  
          div svg {
            width: 17px;
            height: 17px;
  
            path {
              fill: #23BCFA;
            }
  
            &:hover {
              path {
                fill: #23BCFA
              }
            }
          }
        }
      }
    }
  }

  .buttons {
    width: 100%;

    .button {
      width: 100%;
      height: 47px;

      &:first-child {
        margin-bottom: 10px;
      }
    }
  }
}

@include onTablet {
  .smartBlock {
    padding: 50px 40px 15px;

    .name {
      p {
        text-align: left;
      }
  
      .contractName {
        align-items: center;
        justify-content: space-between;
  
        h2 {
          display: inline-block;
          text-align: left;
          width: min-content;
          margin: 20px 0 20px;
        }
      }
    }

    .description {
      border-bottom: none;
      margin-bottom: 15px;

      .descriptionItem {
        margin-bottom: 10px;
      }
    }
  
    .extraInfo {
      display: flex;
      justify-content: space-between;
      margin-bottom: 50px;
  
      .firstItem {
        padding-bottom: 0;
        border-bottom: none;
        border-left: 2px solid rgb(14%, 74%, 98%, 1);
  
        p {
          margin: 0;
          margin-left: 5px;
        }
        span {
          font-weight: bold;
          font-size: 14px;
        };
      }
  
      .secondItem {
        text-align: center;
        padding-bottom: 0;
        border-left: 2px solid rgb(14%, 74%, 98%, 1);
  
        p {
          margin: 0;
          text-align: left;
          margin-left: 5px;
        }
  
        div {
          margin-left: 10px;
        }
      }
    }
  
    .buttons {
      display: flex;
      justify-content: space-between;
  
      .button {
        width: 45%;
      }
    }
  }
}

