@import '../../../../assets/style/variable.scss';
@import '../../../../assets/style/mixins.scss';

.panel {
  display: none;
}

.mobileText {
  margin: 0;
  padding: 0;
}

.contract {
  height: 104px;
  background-color: #213054;
  border-radius: 15px;
  font-size: 12px;
  margin-bottom: 10px;

  .firstPart {
    display: flex;
    align-items: center;
    height: 50px;

    .contractName {
      flex: 0 0 75%;
      display: flex;

      .contractIcon {
        margin-left: 30px;
        margin-right: 10px;
        
        div svg {
          width: 14px;
          height: 17px;

          path {
            fill: #23BCFA;
          }
        }
      }

      .Gold {
        div svg {
          width: 14px;
          height: 17px;
          
          path {
            fill: #F4C41A;
          }
        }
      }

      .Mango {
        div svg {
          width: 14px;
          height: 17px;
          
          path {
            fill: rgb(96%, 60%, 10%, 1);
          }
        }
      }
    }

    .amount {
      display: flex;
      flex-direction: column;
      position: relative;
      
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid white;
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
    }

      span {
        font-size: 14px;
      }
    }
  }

  .secondPart {
    background-color: rgb(14%, 74%, 98%, 0.8);;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 54px;

    .price {
      margin-left: 20px;
      display: flex;
      flex-direction: column;
    }

    .income {
      display: flex;
      flex-direction: column;
      position: relative;
      
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid white;
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }
    }

    .profitability {
      margin-right: 20px;
      display: flex;
      flex-direction: column;
      position: relative;
      
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid white;
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }

      .arrow {
        display: none;
      }
    }
  }
}

span {
  font-weight: bold;
  font-size: 14px;
}

@include onTablet {

  .panel {
    display: flex;
  }

  .contractName {
    flex: 0 0 40%;
  }

  .amount {
    flex: 0 0 10%;
  }

  .price {
    flex: 0 0 15%;
  }

  .income {
    flex: 0 0 15%;
  }

  .profitability {
    flex: 0 0 20%;
  }

  .mobileText {
    display: none;
  }

  .contract {
    display: flex;
    height: 60px;
  
    .firstPart {
      height: 60px;
      flex: 0 0 50%;
  
      .contractName {
        flex: 0 0 75%;
      }

      .amount {
        flex: 0 0 25%;
        display: flex;
        
        &::before {
          top: 8px;
        }
      }
    }
  
    .secondPart {
      flex: 0 0 50%;
      background-color: inherit;
      align-items: center;
      justify-content: space-between;
      height: 60px;

      .price {
        flex: 0 0 30%;
        margin-left: 0;
        display: flex;
        flex-direction: column;
      }
  
      .income {
        flex: 0 0 30%;
        &::before {
          top: 8px;
        }
      }
  
      .profitability {
        margin-right: 0;
        flex: 0 0 20%;
        
        &::before {
          top: 8px;
        }
      }

      .arrow {
        flex: 0 0 20%;
        display: block;

        .arrowWrapper {
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 37px;
          height: 37px;
          background: rgb(14%, 74%, 98%, 0.15);
          border-radius: 15%;
        }
      }
    }
  }
  
  span {
    font-weight: bold;
    font-size: 14px;
  }
}

