@include onFull {
  .mobileText {
    display: none;
  }

  .contract {
    display: flex;
    height: 89px;

    span {
      font-weight: bold;
      font-size: 24px;
    }
  
    .firstPart {
      height: 89px;
      flex: 0 0 50%;
  
      .contractName {
        flex: 0 0 75%;

        .contractIcon {
          div svg {
            width: 20px;
            height: 24px;
          }
        }
      }

      .amount {
        flex: 0 0 25%;
        display: flex;
        
        &::before {
          top: 14px;
        }

        span {
          font-size: 24px;
        }
      }
    }
  
    .secondPart {
      flex: 0 0 50%;
      background-color: inherit;
      align-items: center;
      justify-content: space-between;
      height: 89px;

      .price {
        flex: 0 0 30%;
        margin-left: 0;
        display: flex;
        flex-direction: column;
      }
  
      .income {
        flex: 0 0 30%;
        &::before {
          top: 14px;
        }
      }
  
      .profitability {
        margin-right: 0;
        flex: 0 0 20%;
        
        &::before {
          top: 14px;
        }
      }

      .arrow {
        flex: 0 0 20%;
        display: block;

        .arrowWrapper {
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 59px;
          height: 59px;
          background: rgb(14%, 74%, 98%, 0.15);
          border-radius: 15%;
          transition: 300ms;

          &:hover {
            background: rgb(14%, 74%, 98%, 0.4);
          }

          .arrowIcon {
            div svg {
              width: 11px;
              height: 22px;

            }
          }
        }
      }
    }
  }
}
