@import '../../../assets/style/variable';
@import '../../../assets/style/mixins.scss';

.button {
  background: var(--green);
  font-size: 18px;
  color: var(--white);
  border-radius: 15px;
  border: none;
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: 300ms;

  &:hover {
    background: var(--blue);
  }

  &:target {
    background-color: #219DD5;
  }
}

.button span {
  font-size: 14px;
}

.green {
  background: var(--green);

  &:hover {
    background: var(--blue);
  }
}

.gold {
  background: rgb(96%, 77%, 10%, 0.8);;

  &:hover {
    background: rgb(96%, 77%, 10%, 1);;
  }
}

.brown {
  background: rgb(96%, 60%, 10%, 0.6);

  &:hover {
    background: rgb(96%, 60%, 10%, 0.8);
  }
}