@import '../../../assets/style/variable';
@import '../../../assets/style/mixins.scss';

.wrapper {
  box-sizing: border-box;

  @include onTablet {
    margin: 20px;
    flex: 1 1 50%;
  }

  @include onNotebook {
    margin: 20px;
    flex: 1 1 50%;
  }

  @include onFull {
    margin: 20px;
    flex: 1 1 50%;
  }
}

.DNCBalance {
  background: var(--gradienBlack);
  border-radius: 15px;
  margin: 0 auto 20px;
  color: var(--white);
  padding-bottom: 30px;

  @include onTablet {
    width: 100%;
  }

  @include onNotebook {
    width: 100%;
    margin: 0 auto;
  }
}

.blockName {
  display: flex;
  justify-content: center;
  margin: 0 auto 3px;
  padding-top: 27px;
}

.DNCBalanceIcon {
  width: 20px;
  height: 20px;
}

.blockName span {
  margin-left: 5px;
}

.balance {
  display: flex;
  justify-content: center;
  font-weight: bold;
  font-size: 36px;
  line-height: 36px;
  margin-bottom: 12px;
}

.todayIncome {
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  color: var(--blue);
}

.todayIncome span {
  font-weight: normal;
  color: var(--white);
}

.schedule {
  margin-top: 40px;
  text-align: center;
  font-size: 48px;
  color: var(--blue);

  .scheduleIcon, .numbers {
    div {
      svg {
        width: 100%;
      }
    }
  }

  .numbers {
    margin-bottom: 18px;
    text-align: center;
  }
}