@import '../../assets/style/variable';
@import '../../assets/style/mixins.scss';

.dashboard {
  background: var(--background);
  display: flex;
  flex-direction: column;
  justify-content: center;

  @include onTablet {
    flex-direction: row;
    flex-wrap: wrap;
  }

  @include onNotebook {
    flex-direction: row;
    flex-wrap: wrap;
  }

}

ol, ul {
	list-style: none;
}

.PanelBlock {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 360px;
  padding: 0 20px;

  @include onTablet {
    width: 100%;
  }

  @include onNotebook {
    width: 100%;
    flex-direction: row;
  }

  @include onFull {
    width: 100%;
  }

  .balance {
    flex: 1 1 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  
    @include onTablet {
      flex-wrap: nowrap;
    }
  
    @include onNotebook {
      flex-direction: row;
      flex-wrap: nowrap;
      flex: 1 1 45%;
    }

    @include onFull {
      flex-direction: row;
      flex-wrap: nowrap;
      flex: 1 1 50%;
    }
  }
  
  .otherBlocks {
    flex: 1 1 100%;
    display: flex;
    flex-direction: column;
  
    @include onNotebook {
      display: flex;
      flex: 0 0 50%;
      flex-direction: column;
    }
  
    @include onFull {
      display: flex;
      flex: 0 0 50%;
      flex-direction: column;
    }
  
    .contracts {
      flex: 1 1 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    
      @include onTablet {
        flex-wrap: nowrap;
      }
    
      @include onNotebook {
        flex-wrap: nowrap;
        flex: 1 1 50%;
      }

      @include onFull {
        flex-wrap: nowrap;
        flex: 1 1 50%;
      }
    }
    
    .other {
      flex: 1 1 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    
      @include onTablet {
        flex-wrap: nowrap;
      }
    
      @include onNotebook {
        flex-wrap: nowrap;
        flex: 1 1 50%;
      }

      @include onFull {
        flex-wrap: nowrap;
        flex: 1 1 50%;
      }
    }
  }
}

