@import '../../../../assets/style/variable.scss';
@import '../../../../assets/style/mixins.scss';

p {
  margin: 0;
}

.buyContracts {
  background-color: var(--darkBlue);
  height: 100%;
  width: 100%;
  border-radius: 15px;
  color: var(--white);
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
  user-select: none;

  @include onTablet {
  }

  @include onNotebook {
  }

  @include onFull {
    padding: 10px 30px;
  }

  .header {
    flex: 1 1 1fr;
    display: flex;
    text-align: center;
    flex-direction: column;
    border-bottom: 1px solid rgb(14%, 74%, 98%, 0.2);
    margin-bottom: 10px;

    .infoString {
      font-size: 14px;
      display: flex;
      color: var(--blueDark);
      justify-content: center;
      align-items: center;

      .infoIcon {
        margin-right: 10px;
        div svg {
          width: 20px;
          height: 20px;
      
          path {
            fill: #23BCFA;
          }
        }
      }
    }

    .subtitle {
      display: flex;
      flex-direction: column;
      font-size: 13px;
      margin-bottom: 5px;
    }

    .name {
      font-weight: bold;
      font-size: 18px;
      margin-bottom: 10px;
    }
  }

  .main {
    flex: 1 1 50px;
    display: flex;
    font-size: 14px;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 10px;

    .reverseIcon {
      margin: 0 20px;

      div svg {
        width: 16px;
        height: 14px;
    
        path {
          fill: #23BCFA;
        }
      }
    }
  }

  .chooseAmount {
    text-align: center;
    font-size: 14px;
    color: #ABE7FF;
    margin-bottom: 5px;
  }

  .count {
    display: flex;
    justify-content: space-between;
    background-color: #2276A7;
    border-radius: 10px;
    align-items: center;
    overflow: hidden;
    margin-bottom: 20px;

    @include onTablet {
    }
  
    @include onNotebook {
    }
  
    @include onFull {
      height: 70px;
      flex: 0 0 70px;
    }

    .countIcon {
      margin-top: 5px;
      cursor: pointer;
      transition: 150ms;

      &:active {
        transform: scale(0.9);
      }

      div svg {
        width: 50px;
        height: 50px;

        @include onFull {
          width: 75px;
          height: 75px;
        }
    
        path {
          fill: #23BCFA;
        }
      }
    }
  }

  .buttonBlock {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex: 1 1 40%;


    .rules {
      display: flex;
      margin-bottom: 10px;

      .checkbox {
        flex: 0 0 20px;
        display: block;
        width: 20px;
        height: 20px;
        background-color: inherit;
        border: 1px solid var(--blueDark);
        border-radius: 15%;
        margin-right: 5px;
      }

      .rulesText {
        flex: 1 1 1fr;
        font-size: 12px;

        span {
          font-size: 12px;
          color: var(--blueDark);
        }
      }
    }

    .button {
      height: 50px;
      display: flex;
      border-radius: 15px;
      cursor: pointer;
      transition: 300ms;

      &:hover {
        box-shadow: 0 0 10px var(--blueDark);
        transform: translateY(-3px);
      }

      &:active {
        transform: scale(0.95);
      }

      @include onTablet {
      }
    
      @include onNotebook {
      }
    
      @include onFull {
        height: 70px;
      }

      .left {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 14px;
        flex: 0 0 60%;
        background-color: #00AEAC;
        border-top-left-radius: 15px;
        border-bottom-left-radius: 15px;
      }

      .right {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 10px;
        flex: 0 0 40%;
        background: var(--gradienBlack);
        border-top-right-radius: 15px;
        border-bottom-right-radius: 15px;

        div {
          display: flex;

          span {
            font-weight: bold;
            font-size: 12px;
            margin-right: 5px;
          }
        }
      }
    }
  }
}



.DNCCoin {
  div svg {
    width: 17px;
    height: 17px;

    path {
      fill: #23BCFA;
    }
  }
}

.contractIcon {
  div svg {
    width: 16px;
    height: 20px;

    path {
      fill: #23BCFA;
    }
  }
}

.fakeButton {
  height: 50px;
  display: flex;
  border-radius: 15px;
  cursor: pointer;
  transition: 300ms;
  position: relative;
  overflow: hidden;

  .wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: grey;
    opacity: 0.5;
  }

  @include onTablet {
  }

  @include onNotebook {
  }

  @include onFull {
    height: 70px;
  }

  .left {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    flex: 0 0 60%;
    background-color: #00AEAC;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
  }

  .right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    flex: 0 0 40%;
    background: var(--gradienBlack);
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;

    div {
      display: flex;

      span {
        font-weight: bold;
        font-size: 12px;
        margin-right: 5px;
      }
    }
  }
}
