@import '../../assets/style/variable.scss';
@import '../../assets/style/mixins.scss';

.historyPage {
  max-width: 1920px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 82px 20px 0;
  width: 100%;
  height: 100%;
  background: var(--background);
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include onTablet {
    flex-direction: column;
    padding: 82px 20px 0 80px;
  }

  @include onNotebook {
    padding: 62px 20px 0 70px;
    flex-direction: row;
  }

  @include onFull {
    padding: 82px 30px 0 140px;
    flex-direction: row;
  }

  .HistoryList {
    flex: 0 0 70%;
  }

  .Statistic {
    flex: 0 0 27%;
  }
}
