@import '../../../assets/style/variable.scss';
@import '../../../assets/style/mixins.scss';

.title {
  text-align: left;
  font-size: 16px;
  color: var(--green);
  margin: 0 0px;

  @include onFull {
    margin: 30px 30px 10px;
    font-size: 24px;
  }
}

.ContractsPage {
  max-width: 1920px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 82px 0;
  width: 100%;
  height: 100%;
  background: var(--background);

  @include onTablet {
    padding: 82px 0 0 80px;
  }

  @include onNotebook {
    padding: 62px 0 0 70px;
  }

  @include onFull {
    padding: 82px 0 0 125px;
  }
}

.ContractsFirstBlock {
  display: flex;
  background-color: rgb(14%, 74%, 98%, 0.15);
  color: var(--white);
  padding: 12px;
  line-height: 22px;
  font-size: 18px;
  border-radius: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  justify-content: center;
  align-items: center;

  .contractIcon {
    margin-right: 10px;
  }
}

.contractsBlock {
  display: flex;
  flex-direction: column;
  align-items: stretch;

  @include onTablet {
    justify-content: start;
    flex-direction: row;
    flex-wrap: wrap;
  }

  @include onNotebook {
    justify-content: start;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .contractItem {
    flex: 1 1 0;
    margin: 0 auto;
    width: 100%;
    max-width: 450px;
    padding: 10px;

    @include onTablet {
      justify-content: start;
      flex-direction: row;
      flex-wrap: wrap;
      flex: 0 0 50%;
    }
  
    @include onNotebook {
      justify-content: start;
      flex-direction: row;
      flex-wrap: wrap;
      flex: 0 0 50%;
    }

    @include onFull {
      justify-content: start;
      flex-direction: row;
      flex-wrap: wrap;
      max-width: 550px;
    }
  }
}

.PanelBlock {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 360px;
  padding: 0 20px;

  @include onTablet {
    width: 100%;
  }

  @include onNotebook {
    width: 100%;
    flex-direction: row;
  }

  @include onFull {
    width: 100%;
  }

  .balance {
    flex: 1 1 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  
    @include onTablet {
      flex-wrap: nowrap;
    }
  
    @include onNotebook {
      flex-direction: row;
      flex-wrap: nowrap;
      flex: 1 1 50%;
    }

    @include onFull {
      flex-direction: row;
      flex-wrap: nowrap;
      flex: 1 1 50%;
    }
  }
  
  .otherBlocks {
    flex: 1 1 100%;
    display: flex;
    flex-direction: column;

    @include onTablet {
      flex-direction: row;
    }
  
    @include onNotebook {
      flex: 0 0 50%;
      flex-direction: row;
    }
  
    @include onFull {
      flex: 0 0 50%;
      flex-direction: row;
    }
  
    .contracts {
      flex: 1 1 100%;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
    
      @include onTablet {
        flex: 0 0 50%;
      }
    
      @include onNotebook {
        flex: 1 1 50%;
      }

      @include onFull {
        flex: 1 1 50%;
      }
    }
    
    .BuyContracts {
      padding: 20px;
      flex: 1 1 100%;
    
      @include onTablet {
        flex: 0 0 50%;
      }
    
      @include onNotebook {
        flex: 1 1 50%;
      }

      @include onFull {
        flex: 1 1 50%;
      }
    }
  }
}
