@import '../../../assets/style/variable';
@import '../../../assets/style/mixins.scss';

.incomeBlock {
  padding: 20px;
}

.ActiveFilterIcon, .filterIcon {
  margin-left: 10px;
}

.panel {
  display: none;
  color: var(--white);
  margin-left: 20px;
  border-top: 1px solid var(--greyDark);
  padding: 20px 0;

  @include onTablet {
    display: flex;
  }

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
    padding-left: 25px;
  }

  .sortByIndex {
    user-select:none;
    cursor: pointer;
    display: flex;
    flex: 1 1 25%;
  }

  .sortByAmount {
    user-select:none;
    cursor: pointer;
    display: flex;
    justify-content: left;
    flex: 1 1 25%;
  }

  .sortByIncome {
    user-select:none;
    display: flex;
    cursor: pointer;
    justify-content: left;
    flex: 1 1 25%;
  }

  .sortByDate {
    user-select:none;
    display: flex;
    justify-content: left;
    flex: 1 1 25%;
    cursor: pointer;
  }
}

.list {
  width: 100%;
  margin: 0 auto;
}

.item {
  width: 100%;
  height: 104px;
  background-color: #213054;
  border-radius: 15px;
  font-size: 12px;
  margin-bottom: 10px;
  color: var(--white);
  font-size: 14px;
  font-weight: bold;
  margin-right: 20px;

  @include onTablet {
    .item {
      display: flex;
      height: 60px;
    }
  }

  @include onNotebook {
    .item {
      display: flex;
      height: 60px;
    }
  }

  @include onFull {
    .item {
      display: flex;
      height: 60px;
    }
  }
}

ul {
  margin: 0;
  padding: 0;
}
