@import '../../../assets/style/variable';
@import '../../../assets/style/mixins.scss';

.wrapper {
  box-sizing: border-box;

  @include onTablet {
    margin: 20px;
    flex: 1 1 50%;
  }

  @include onNotebook {
    margin: 20px;
    flex: 1 1 50%;
  }

  @include onFull {
    margin: 20px;
    flex: 1 1 50%;
  }
}

.NumberOfPartners {
  color: var(--white);
  background: var(--darkBlue);
  width: 320px;
  height: 173px;
  border-radius: 15px;
  margin: 0 auto 20px;
  padding: 20px;
  box-sizing: border-box;

  @include onTablet {
    width: 100%;
    height: 100%;
  }

  @include onNotebook {
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }

  @include onFull {
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }
}

.iconBox {
  display: flex;
  width: 49px;
  height: 49px;
  border-radius: 15px;
  background: rgb(44%, 90%, 88%, 0.1);
  justify-content: center;
  align-items: center;
  margin: 0 auto 20px;

  @include onNotebook {
    margin: 0 0 20px 0;
  }

  @include onFull {
    margin: 0 0 20px 0;
  }
}

.sum {
  display: flex;
  justify-content: center;
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 8px;

  @include onNotebook {
    justify-content: flex-start;
  }

  @include onFull {
    justify-content: flex-start;
  }
}

.partnersPhoto {
  display: flex;
  margin-right: 15px;

  & .partnerPhoto {
    border-radius: 50%;
    width: 45px;
    height: 45px;
    overflow: hidden;

    &:nth-child(2) {
      transform: translateX(-15px);
    }

    & img {
      width: 100%;
      height: 100%;
    }
  }
}

.text {
  text-align: center;
  font-size: 12px;
  color: var(--white);

  @include onNotebook {
    text-align: left;
  }

  
  @include onFull {
    text-align: left;
  }
}
