@import '../../../../assets/style/variable';
@import '../../../../assets/style/mixins.scss';

.coin {
  margin-left: 5px;

  div svg {
    width: 14px;
    height: 14px;

    @include onFull {
      width: 24px;
      height: 24px;
    }
  }
}

.mobileText {
  font-size: 12px;
  color: rgb(100%, 100%, 100%, 0.6);

  @include onTablet {
    display: none;
  }

  @include onNotebook {
    display: none;
  }

  @include onFull {
    display: none;
  }
}

.date {
  display: none;

  @include onTablet {
    display: flex;
  }

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
  }
}

.amount {
  display: none;

  @include onTablet {
    display: flex;
  }

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
  }
}

.MobileDate, .MobileAmount {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  position: relative;

  div {
    display: flex;
    justify-content: left;
  }

  @include onTablet {
    display: none;
  }

  @include onNotebook {
    display: none;
  }

  @include onFull {
    display: none;
  }
}
.MobileDate {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    display: inline-block;
    top: 15px;
    border-top: 1.5px solid rgb(100%, 100%, 100%, 0.6);
    width: 25px;
    margin: 0 -20px;
    transform: rotate(90deg);
  }
}

.item {
  width: 100%;
  height: 104px;
  background-color: #213054;
  border-radius: 15px;
  font-size: 12px;
  margin-bottom: 10px;
  color: var(--white);
  font-size: 14px;
  font-weight: bold;

  .firstPart {
    display: flex;
    align-items: center;
    height: 50px;

    .index {
      margin-left: 10px;
      flex: 0 0 50%;
      display: flex;
    }

    .amount {
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid rgb(100%, 100%, 100%, 0.6);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }
    }
  }

  .secondPart {
    background-color: rgb(14%, 74%, 98%, 0.8);;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 54px;
    padding: 0 10px;

    .income {
      margin-left: 20px;
      display: flex;
      flex-direction: column;
      position: relative;

      div {
        display: flex;
      }

      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid rgb(100%, 100%, 100%, 0.6);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }
    }

    .date {
      margin-right: 20px;
      flex-direction: column;
      position: relative;
      
      &::before {
        content: "";
        position: absolute;
        display: inline-block;
        top: 15px;
        border-top: 1.5px solid rgb(100%, 100%, 100%, 0.6);
        width: 25px;
        margin: 0 -20px;
        transform: rotate(90deg);
      }

      .arrow {
        display: none;
      }
    }
  }
}


@include onTablet {
  .item {
    display: flex;
    height: 60px;
  
    .firstPart {
      height: 60px;
      flex: 1 1 50%;
  
      .index {
        display: flex;
        justify-content: left;
        flex: 1 1 50%;
      }

      .amount {
        flex: 1 1 50%;
        display: flex;
        justify-content: left;
        padding-right: 20px;
        
        &::before {
          top: 8px;
        }
      }
    }
  
    .secondPart {
      flex: 1 1 50%;
      background-color: inherit;
      display: flex;
      height: 60px;

      .income {
        flex: 1 1 50%;
        display: flex;
        justify-content: left;
        flex-direction: column;
        padding-left: 20px;

        &::before {
          top: 10px;
        }
      }
  
      .date {
        margin-right: 0;
        flex: 1 1 50%;
        display: flex;
        justify-content: left;
        
        &::before {
          top: 8px;
        }
      }

      .rejectContract {
        display: flex;
        justify-content: center;
        flex: 0 0 20%;

      }

      .rejectContractIcon {
        display: flex;
      }
    }
  }
}

@include onNotebook {
  .item {
    display: flex;
    height: 60px;
  
    .firstPart {
      height: 60px;
      flex: 1 1 50%;
  
      .index {
        display: flex;
        justify-content: left;
        flex: 1 1 50%;
      }

      .amount {
        flex: 1 1 50%;
        display: flex;
        justify-content: left;
        padding-right: 20px;
      }
    }
  
    .secondPart {
      flex: 1 1 50%;
      background-color: inherit;
      align-items: center;
      justify-content: left;
      height: 60px;

      .income {
        flex: 1 1 50%;
        margin-left: 0;
        display: flex;
        flex-direction: column;
        justify-content: left;
        padding-left: 20px;

        &::before {
          top: 10px;
        }
      }
  
      .date {
        margin-right: 0;
        flex: 1 1 50%;
        justify-content: left;
        
        &::before {
          top: 8px;
        }
      }
    }
  }
}

@include onFull {
  .item {
    display: flex;
    height: 89px;
    font-size: 24px;
    padding-left: 25px;
  
    .firstPart {
      height: 89px;
      flex: 1 1 50%;
  
      .index {
        display: flex;
        justify-content: left;
        flex: 1 1 50%;
      }

      .amount {
        flex: 1 1 50%;
        display: flex;
        align-items: flex-start;
        justify-content: left;
        padding-right: 20px;
      }
    }
  
    .secondPart {
      flex: 1 1 50%;
      background-color: inherit;
      height: 89px;

      .income {
        flex: 1 1 50%;
        margin-left: 0;
        display: flex;
        justify-content: left;
        flex-direction: column;
        padding-left: 20px;

        &::before {
          content: "";
          position: absolute;
          display: inline-block;
          top: 15px;
          left: 0;
          width: 40px;
        }
      }
  
      .date {
        margin-left: 50px;
        padding-left: 30px;
        margin-right: 0;
        flex: 1 1 50%;
        justify-content: left;
        
        &::before {
          content: "";
          position: absolute;
          display: inline-block;
          top: 15px;
          left: 0;
          width: 40px;
        }
      }
    }
  }
}
