body {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

* {
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

button {
    border: none;
    outline: none;
}

ul li {
    list-style: none;
}

.app {
    background: var(--background);
    overflow: hidden;
    min-height: 100%;
    background-color: black;
}