@import '../../../assets/style/variable';
@import '../../../assets/style/mixins.scss';

.contractListBlock {
  padding: 20px;
}

.ActiveFilterIcon, .filterIcon {
  margin-left: 10px;
}

.panel {
  display: none;
  color: var(--white);
  border-top: 1px solid var(--greyDark);
  padding: 20px 0;

  @include onTablet {
    display: flex;
  }

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
  }

  .sortByIndex {
    user-select:none;
    cursor: pointer;
    display: flex;
    flex: 0 0 20%;
    justify-content: center;
  }

  .sortByAmount {
    user-select:none;
    cursor: pointer;
    display: flex;
    justify-content: center;
    flex: 0 0 15%;
  }

  .sortByIncomeDNC {
    user-select:none;
    display: flex;
    cursor: pointer;
    justify-content: center;
    flex: 0 0 15%;

  }

  .sortByIncomeRZM {
    user-select:none;
    display: flex;
    flex: 0 0 15%;
    justify-content: center;
    cursor: pointer;

  }

  .sortByDate {
    user-select:none;
    display: flex;
    justify-content: left;
    margin-left: 75px;
    flex: 0 0 30%;
    cursor: pointer;

    @include onNotebook {
      margin-left: 100px;
    }
  
    @include onFull {
      margin-left: 150px;
    }
  }
}

.list {
  width: 100%;
  margin: 0 auto;
}

.item {
  width: 100%;
  background-color: #213054;
  border-radius: 15px;
  font-size: 12px;
  margin-bottom: 10px;
  color: var(--white);
  font-size: 14px;
  font-weight: bold;
  margin-right: 20px;

  @include onTablet {
    .item {
      display: flex;
      height: 60px;
    }
  }

  @include onNotebook {
    .item {
      display: flex;
      height: 60px;
    }
  }

  @include onFull {
    .item {
      display: flex;
      height: 60px;
    }
  }
}

ul {
  margin: 0;
  padding: 0;
}
