@import '../../../../assets/style/variable';
@import '../../../../assets/style/mixins.scss';

.title {
  color: #56B6B5;
  text-align: center;

  @include onTablet {
    margin-left: 25px;
    text-align: left;
  }
  
  @include onNotebook {
    margin-left: 25px;
    text-align: left;
  }
}

.newsList {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  margin-bottom: 30px;
  background-color: var(--background);

  @include onTablet {
    flex-direction: row;
    flex-wrap: wrap;
  }
  
  @include onNotebook {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .newsItem {
    display: flex;
    flex-direction: column;
    width: 320px;
    margin: 0 auto 20px;
    border-radius: 15px;
    background: var(--darkBlue);
    color: var(--white);
    box-sizing: border-box;
    cursor: pointer;
    transition: 300ms;
  
    &:hover {
      box-shadow: 0 0 10px var(--blueDark);
    }
  
    @include onTablet {
      flex: 1 1 45%;
      margin: 10px;
      width: 45%;
    }
  
    @include onNotebook {
      flex-wrap: wrap;
      width: 25%;
      flex: 1 1 25%;
      margin: 10px;
    }
  
    @include upHorizontalTablet {
      flex-wrap: wrap;
      width: 24%;
      flex: 1 1 23%;
      max-width: 23.8%;
      margin: 10px;
    }
  }
}

.yes {
  width: 320px;
  height: 47px;
  margin:  0 auto 50px;

  @include onTablet {
    width: 139px;
  }

  @include onNotebook {
    width: 139px;
  }

  @include onFull {
    width: 195px;
    height: 62px;
  }
}

.no {
  display: none;
}
