@import '../../../assets/style/variable.scss';
@import '../../../assets/style/mixins.scss';

.contracts {
  z-index: 1000;
  margin: 0 10px;
  color: white;
  overflow: hidden;

  h2 {
    text-align: left;
    font-size: 16px;
    color: var(--green)
  }
}

.panel {
  display: none;
}

@include onTablet {
  .panel {
    display: flex;
    margin-bottom: 10px;
  }

  .contractName {
    flex: 0 0 35%;
    padding-left: 25px;
  }

  .amount {
    flex: 0 0 15%;
  }

  .price {
    flex: 0 0 15%;
  }

  .income {
    flex: 0 0 15%;
  }

  .profitability {
    flex: 0 0 20%;
  }
}

@include onNotebook {
  .panel {
    display: flex;
    margin-bottom: 10px;
  }

  .contractName {
    flex: 0 0 35%;
    padding-left: 25px;
  }

  .amount {
    flex: 0 0 15%;
  }

  .price {
    flex: 0 0 15%;
  }

  .income {
    flex: 0 0 15%;
  }

  .profitability {
    flex: 0 0 20%;
  }
}

@include onFull {
  .contracts {
    margin: 0 20px;

    h2 {
      font-size: 24px;
    }
  }

  .panel {
    display: flex;
    margin-bottom: 10px;
  }

  .contractName {
    flex: 0 0 37%;
    padding-left: 25px;
  }

  .amount {
    flex: 0 0 13%;
  }

  .price {
    flex: 0 0 15%;
  }

  .income {
    flex: 0 0 15%;
  }

  .profitability {
    flex: 0 0 20%;
  }
}