@import '../../../../../assets/style/variable';
@import '../../../../../assets/style/mixins';

.item {
  width: 100%;
  a {
    width: 100%;
    display: flex;
  }

  .text {
    display: none;


    @include onTablet {
      display: flex;
      align-items: center;
    }
  
    @include onNotebook {
      display: flex;
      align-items: center;
    }
  
    @include onFull {
      display: flex;
      align-items: center;
    }
  }
}

.nav {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: var(--gradienBlack);
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  overflow: hidden;
  transition: 300ms;
  width: 112px;
  z-index: 2000;

  &:hover {
    .historyIcon {
      display: none;
    }

    .historyOpenIcon {
      display: flex;
    }

    .bigLogo {
      opacity: 1;
    }
    
    .smallLogo {
      opacity: 0;
    }

    span {
      opacity: 1;
    } 
  }

  @include onTablet {
    width: 67px;
    &:hover {
      width: 170px;
    }
  }

  @include onNotebook {
    width: 67px;
    &:hover {
      width: 170px;
    }
  }

  @include onFull {
    width: 112px;
    &:hover {
      width: 232px;
    }
  }
}

.logo {
  flex: 0 0 15px;
  position: relative;
  text-align: center;
  margin-top: 38px;
  margin-bottom: 34px;
  padding-left: 20px;
  height: 106px;

  @include onTablet {
  }

  @include onFull {
    text-align: center;
    padding-left: 40px;
    margin-top: 38px;
  }
  
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;

  @include onFull {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}

.item {
  display: flex;
  position: relative;
  cursor: pointer;
  width: 100%;
  height: 70px;
  text-align: left;
  vertical-align: middle;

  &:hover .text {
    color: var(--blue);
  }

  &:target {
    background-color: rgb(14%, 74%, 98%, 0.15);
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
  }

  @include onTablet {
  }

  @include onFull {
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 70px;
      width: 10px;
      color: var(--blue);
    }
  }

  span {
    transition: 300ms;
    opacity: 0;
  }
}

.dashboardIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 20px;
    vertical-align: middle;
  }

  @include onNotebook {
    padding-left: 20px;
    vertical-align: middle;
  }

  @include onFull {
    padding-left: 40px;
  }
}

.contactsIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 23px;
  }

  @include onNotebook {
    padding-left: 23px;
  }

  @include onFull {
    padding-left: 43px;
  }
}

.icon {
  display: flex;
  align-items: center;

  div {
    width: 40px;
  }

  @include onTablet {
    padding-left: 22px;
  }

  @include onNotebook {
    padding-left: 22px;
  }

  @include onFull {
    padding-left: 42px;
  }
}

.partnersIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 22px;
  }

  @include onNotebook {
    padding-left: 22px;
  }

  @include onFull {
    padding-left: 42px;
  }
}

.text {
  color: var(--white);
  padding-left: 16px;
  display: flex;
  align-items: center;

  @include onTablet {
  }

  @include onFull {
    padding-left: 16px;
  }
}

.history {
  margin-top: auto;
  cursor: pointer;
  margin-bottom: 15px;

  @include onTablet {
    padding-left: 10px;
  }

  @include onNotebook {
    padding-left: 10px;
  }

  @include onFull {
    padding-left: 30px;
  }
}

.active {
  background-color: rgb(14%, 74%, 98%, 0.15);;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;

  &::after {
    content: '';
    position: absolute;
    border-color: var(--blue);
    border-style: solid;
    border-width: 0 2px 0 0;
    border-radius: 10px;
    height: 100%;
    top: 0;
    left: 0;
    width: 1px;
  }

  &::before {
    content: '';
    position: absolute;
    border-color: var(--blue);
    border-style: solid;
    border-width: 0 3px 0 0;
    border-radius: 15px;
    height: 60%;
    top: 20%;
    right: 0;
    width: 1px;
  }
}

.historyIcon {
  display: flex;
  align-items: center;
  width: 41px;
  height: 41px;
  margin-bottom: 3px;
}

.historyOpenIcon {
  display: none;
  align-items: center;
  width: 140px;
  height: 47px;
}

.bigLogo {
  display: flex;
  position: absolute;
  opacity: 0;
  align-items: center;
  justify-content: center;
  transition: 300ms;
}

.smallLogo {
  position: absolute;
  align-items: center;
  justify-content: center;
  display: flex;
  opacity: 1;
  transition: 300ms;
}

.closeMenuIcon {
  display: flex;

  
  @include onTablet {
    display: none;
  }

  @include onNotebook {
    display: none;
  }

  @include onFull {
    display: none;
  }
}