@import '../../assets/style/variable.scss';
@import '../../assets/style/mixins.scss';

.CalendarPage {
  height: 100%;
  background: var(--background);

  @include onNotebook {
    display: flex;
  }

  @include onFull {
    display: flex;
  }

  .eventsBlock {
    @include onNotebook {
      flex: 0 0 30%;
    }
  
    @include onFull {
      flex: 0 0 30%;
    }

    .eventsTitle {
      font-size: 16px;
      font-style: normal;
      font-weight: 600;
      line-height: 20px;
      letter-spacing: 0;
      text-align: left;
      color: rgba(86, 182, 181, 1);
    }

  }

  .calendarBlock {
    @include onNotebook {
      flex: 0 0 70%;
    }
  
    @include onFull {
      flex: 0 0 70%;
    }
  }
}
