@import '../../../assets/style/variable';
@import '../../../assets/style/mixins.scss';


.item {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto 20px;
  border-radius: 15px;
  background: var(--darkBlue);
  color: var(--white);
  box-sizing: border-box;
  cursor: pointer;
  transition: 300ms;

  @include onTablet {
    flex: 0 0 45%;
    margin: 10px;
    width: 45%;
  }

  @include onNotebook {
    flex-wrap: wrap;
    width: 25%;
    flex: 1 1 25%;
    margin: 10px;
  }

  @include upHorizontalTablet {
    flex-wrap: wrap;
    width: 28%;
    max-width: 28%;
    flex: 1 1 28%;
    margin: 20px;
  }

  .image {
    flex: 0 0 180px;
    width: 100%;
    border-radius: 15px;

    @include onTablet {

    }

    @include upHorizontalTablet {
      flex: 0 0 227px;
    }

    @include onFull {
      flex: 0 0 308px;
    }

    img {
      object-fit: cover;
      border-radius: 15px;
      width: 100%;
      height: 100%;
    }
  }

  .content {
    padding: 15px 20px;
    flex: 0 0 1fr;

    @include onFull {
      padding: 30px 40px;
    }
    
    h2 {
      font-size: 18px;
      margin: 0;
      margin-bottom: 10px;

      @include onFull {
        font-size: 28px;
        line-height: 40px;
      }
    }

    p {
      font-size: 14px;
      line-height: 15px;

      @include onFull {
        font-size: 18px;
        line-height: 25px;
        margin-top: auto;
      }
    }
  }

  .buttons {
    flex: 0 0 1fr;
    padding: 0 20px;
    margin-top: auto;

    .infoButton {
      width: 100%;
      height: 47px;
      margin: 0 auto 20px;
    }

    .linkButton {
      display: none;
    }
  }
}
